@charset 'utf-8';
@import "dist/img","dist/table";
@import "mixins/normalize","mixins/reset";
@import "services/view","services/header","services/slider","services/footer","services/tab","services/list";

.width-hundred{width: 100%;margin-top:20px;}

.black-search-box{
	background-color: #e9e9e9;overflow: hidden;
	.black-search{
		position: relative; width: 85%; margin: 7px auto;
		i.icon-fd-black-search{background: url(../img/icon/icon-fd-black-search.png) center center no-repeat; display: block; width: 25px; height: 21px; background-size: contain; position: absolute; top: 50%; margin-top: -11px; left: 4px;}
		input.search-input{width: 100%; padding-left: 30px; background-color: transparent; height: 30px; border: 1px solid #000; border-radius: 10px;}
	}
}

h1.left-line-title{ 
    border-left: 4px solid #23beb0; padding-left: 10px; margin: 10px 3%;font-size:16px;overflow: hidden;@include media-line-size-height(14px,16px,18px,20px,34px);
}
