@charset 'utf-8';
@import "../dist/cssGraph";

%tab{display: flex;display: -webkit-box; 
	li{-webkit-box-flex: 1; box-flex: 1; -ms-box-flex: 1;}
}

.underline-tab{
	margin: 0px 0px 7% 0px; overflow-x: auto; padding: 0px 3%; text-align: center; white-space: nowrap;
	li{     
		border-bottom: 2px solid #fff; display: inline-block; margin: 0px 1%;@include media-font-size(14px,16px,18px,20px,34px);
		a{display: block;width: 100%;height: 100%;padding: 10px 0px;position: relative; }
		&.active{
			border-bottom-color: #b71b2c;
			.triangle{@include triangle-top(#b71b2c,"100% - 15px","50% - 7px");}
		}
	}
}

//所有的元素都有圆角
.all-radius-checkbox-tab{
	margin: 10px 7%; border: 2px solid #b71b2c; border-radius: 50px; @extend %tab;
	a{display: block; background-color: #fff; color: #323232; padding: 3%; border-radius: 50px; -webkit-box-flex: 1; box-flex: 1; -ms-box-flex: 1; height: auto !important; -ms-flex: 1; text-align: center;@include media-line-size-height();
		&.active{background-color: #b71b2c;color: #fff;}
	}
}

//就左右两边有圆角
.rl-radius-checkbox-tab{
	margin: 10px 7%;@extend %tab;
	a{
		border: 2px solid #b71b2c;display: block; background-color: #fff; color: #323232; padding: 3%; -webkit-box-flex: 1; box-flex: 1; -ms-box-flex: 1; height: auto !important; -ms-flex: 1; text-align: center;border-right-width: 0px;@include media-line-size-height();
		&.active{background-color: #b71b2c;color: #fff;}
		&:first-child{border-radius: 15px 0px 0px 15px;}
		&:last-child{border-radius: 0px 15px 15px 0px;border-right-width: 2px;}
	}
}

.border-blue-tab{
	@extend %tab;
	li{border-bottom: 4px solid #dfdfdf;text-align: center;padding: 3% 0px;cursor: pointer;
		&.active{border-bottom-color:#23beb0;}
	}
}
.tab-content{
	.section{
	    opacity: 0;display: none;transition: opacity .15s linear;
	    &.in{opacity: 1;display: block;}
	}
}