@charset 'utf-8';
@import "exomeBase";
@import "dist/span";
.sidebar{
	width: 25%; background-color: #f3f3f3; overflow-y: auto; position: absolute; top: 45px; box-shadow: 0px 5px 3px rgba(89, 89, 89, 0.77);@include attr-calc(height,"100% - 110px");
	li{padding: 3%; border-bottom: 2px solid #23beb0; color: #23beb0; text-align: center; background-color: #fff;cursor: pointer;}
}

.words{
	padding: 3%; width: 75%; overflow-y: auto; position: absolute; top: 45px; right: 0px; @include attr-calc(height,"100% - 110px");
	li{color: #5b5b5b;margin-bottom: 3%;cursor: pointer;}
}

.like-banner{padding: 3% 40%;}

h1.title{border-bottom: 1px solid #dfdfdf; padding: 8px 5%; font-size: 19px; font-weight: bold; margin: 0px 3%;}
ul.dot-cont{
	margin: 0px 3%;padding: 10px 3%;
	li{
		overflow: hidden;margin-bottom: 10px;
		i{vertical-align: sub;}
		span{
			&.black{@extend %span.black;}
			&.block{display: inline-block; width: 57%; vertical-align: top; padding: 3px 0px;}
		}
	}
}