.table{
	border-collapse: initial;table-layout: fixed;
	thead{
		tr{
			th{}
		}
	}
	tbody{
		tr{
			td{text-align: center;vertical-align: middle;}
		}
	}
}

.table2col{
	@extend .table;
	border: 1px solid #e1e1e1; margin: 20px 3%; width: 94%; border-radius: 15px; padding: 10px 3%;
	thead{
		tr{
			th{border-bottom: 1px solid #e1e1e1; color: #23beb0; padding: 15px 0px;}
		}
	}
	tbody{
		tr{
			td{text-align: center;padding: 15px 0px;vertical-align: middle;}
		}
	}
}

//3列的表
.table3col{
	@extend .table;    border-top: 1px solid #dfdfdf;
    width: 94%;
    margin: 0 auto;
    padding: 20px 0px;
	tbody{
		tr{
			td{padding: 10px 0px;}
		}
	}
}