@charset 'utf-8';
@import "../mixins/common";

.go-target{
	margin-top: 20px;
	li{
	    background-color: #fff;padding: 0px 3%;
	    &:last-child{
	    	a{border-bottom: none;}
	    }
	    a{
			width: 100%; display: block; overflow: hidden;padding: 10px 3%;font-size:30px;line-height: 30px;height: 50px;border-bottom: 1px solid #dfdfdf;@include media-font-size();
			img{float: left; margin-right: 10px; height: 100%;}
			i{
				&.go{display: inline-block; background: url(../img/icon/icon-fd-arrow-right.png) center center no-repeat; width: 17px; height: 30px; float: right;}
			}
		}
	}
}

.show-target{
	margin-top: 20px;
	li{
	    background-color:#fff;margin-bottom: 20px;width: 100%; display: block; overflow: hidden;padding: 10px 3%;@include media-line-size-height();height: auto !important;
		span{float: right;}
	}
}

.note{
	display:block; margin: 0px 4% 4%;padding: 20px 5%;background-color:#fff;border:1px solid #f2f2f2;
	h1{@include media-line-size-height(17px,16px,18px,20px,30px);display:inline-block;vertical-align:middle;width: 70%;text-align: center;}
	img{display:inline-block;vertical-align:middle;width: 20%;}
}

.notebook{
	margin: 0% 7% 0px;box-shadow: 0px 0px 10px #c8c8c8;
	@at-root div.notebook-title{
		background-color: #e9e9e9;
		img{width: 30%;margin: -18px 0px 4% 5%;}
		div.address{
			float: right;width: 55%;margin: 2% 0px 4% 0px;
			p{ @include media-font-size();
				&:first-child{margin: 10% 0px;}
				span{width: 50%;display: inline-block;}
			}
		}
		h1{
			text-align: center;@include media-font-size(20px,1.5em,2em,2em,2.5em);padding: 3% 0px;
		}
	}
	@at-root .notebook-row{
		border-top: 6px solid #b71b2c;
		/*&.notebook-table{
			li{text-align: center;
				&.title{font-weight: bold;}
				span{width: 50%;
					a.link{text-decoration: underline;}
				}
			}
		}*/
		&.notebook-table{
		border:none;margin-top:4%;
			li{text-align: center;border:none;
				span{width: 50%;margin:0 0 1.5% 0;
				img{width:35%;}
				}
			}
		}
		li{
			padding: 10px 3%;border-bottom: 1px solid #dadada;@include media-font-size();
			h2{ box-sizing: content-box;border-bottom: 1px solid #dadada;padding: 10px 0px;margin-bottom: 10px;font-weight: bold;@include media-line-size-height();}
			span{width: 100%;display: inline-block;margin: 0px 0px 10px 0px;@include media-line-size-height();height: auto !important;
				&.cont{width: auto;display: inline;margin-left: 3%;}
			}
		}
	}
}

.ico-explain-func{
	border-top: 1px solid #f4f4f4; box-shadow: 0px 3px 8px #eae9e9; padding: 7.5px 3%;
	@at-root .ico-explain-func-box{
		margin: 0px 0px 15px 0px;
		.summary{background-color: #f9f8f8;padding: 10px 3%;font-size: 14px;line-height: 20px;color:rgba(0, 0, 0, 0.7);text-align: justify;display: none;
			@media screen and (min-width:375px) and (max-width:413px) {font-size: 16px;line-height: 20px;}
			@media screen and (min-width:414px) and (max-width:559px) {font-size: 18px;line-height: 22px; }
			@media screen and (min-width:560px) and (max-width:639px) {font-size: 20px;line-height: 24px; }
			@media screen and (min-width:640px) {font-size: 22px;line-height: 30px; }
		}
	}
	.ico{display: inline-block; width: 22px; height: 25px; float: left; margin: 2.5px 0px;}
	.icon-fd-gene{background: url(../img/icon/icon-fd-gene.png) center center no-repeat;background-size: contain;}
	.explain{display: inline-block; width: calc(100% - 72px); padding-left: 3%; @include media-font-size(16px,16px,18px,22px,25px);line-height: 25px;}
	.func{background: url(../img/icon/icon-fd-arrow-down.png) center center no-repeat; background-size: 80%; display: inline-block; width: 30px; height: 27px; float: right;}
	.func-active{background: url(../img/icon/icon-fd-arrow-up.png) center center no-repeat;background-size: 80%;}
}
.table-header{
	padding: 10px 0px;background-color: #f9f8f8;
	li{
		background-color: #fff; overflow: hidden; padding: 10px 3%;margin: 10px 0px;
		h2{ box-sizing: content-box;border-bottom: 1px solid #dadada;padding: 10px 0px;margin-bottom: 10px;font-weight: bold;@include media-line-size-height();}
		span{width: 100%; display: inline-block; margin-bottom: 15px;@include media-line-size-height();height: auto !important;
			&.cont{width: auto;margin: 0px 0px 0px 3%;word-wrap: break-word;display: inline;}
			a.link{text-decoration: underline;}
		}
		p{display: block; float: left; padding: 2% 0px; border-top: 1px solid #dfdfdf; width: 100%;@include media-line-size-height();height: auto !important;}
	}
}
.editable{
	background-color: #f9f9f9;padding: 0px 3%;
	li{
		padding: 10px 0px;border-bottom: 1px solid #dadada;
		&:last-child{border-bottom: none;}
		span.title{}
		input{background-color: #f9f9f9;border: none;outline: none;
			&:focus{
				text-shadow: 0px 0px 0px #000;color:#23beb0;-webkit-text-fill-color: transparent;
			}
			&:focus + span.save{
				color: #de3e50;
			}
		}
		span.save{
			float: right;color:#989898;
		}
	}
}

.navbar{
	padding: 0px 3%;overflow: hidden;
	li{
		width: 48%; float: left;padding: 5% 0px;background-color:#fff;border:1px solid #f2f2f2;border-radius: 8%;
    	&:nth-of-type(2n+2){margin-left:4%;}
    	&:nth-of-type(1),&:nth-of-type(2){margin-bottom:5%;}
		a{
			text-align: center;display: block;
			img{display: inline-block; margin: 4% auto;width: 20%;vertical-align: middle;}
			span{@include media-line-size-height();margin-left: 6%;}
		}
	}
}

.replace-li{
	@include attr-calc(width,"100% / 3");float: left;padding: 3% 3% 0px;text-align: center;
	img{padding: 0px;width: 100%;}
	span{display: inline-block;margin-top: 10%;color:#323232;opacity:0.8;
		@include media-line-size-height(14px,16px,18px,20px,22px);height: 30px;
		@media screen and (min-width:375px) and (max-width:413px) {height: 34px; }
		@media screen and (min-width:414px) and (max-width:559px) {height: 38px; }
		@media screen and (min-width:560px) and (max-width:639px) {height: 42px; }
		@media screen and (min-width:640px) {height: 48px; }
	}
}

